/* ABOUT */
.about-us {
    background-color: #f4ce14;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 1.25rem 5rem 2.5rem 5rem;
    min-height: 22rem;
    margin-bottom: -1rem;
    grid-gap: 25vw;
  }
  
  .about-subtext {
    margin-top: -1.5rem;
  }
  
  .double-image {
    max-height: 18rem;
    position: relative;
    top: 4rem;
    left: 2rem;
  }
  
  .double-image > img {
    width: 60%;
    height: 100%;
    object-fit: cover;
  }
  
  .about-1 {
    position: relative;
    top: 0;
    left: 0;
  }
  
  .about-2 {
    position: absolute;
    top: -3.5rem;
    left: 5rem;
  }
  